<template>
 <h2 hidden >{{state.circle = $route.params.id }}</h2>
<div class="row container" >
  <span hidden>{{state.circleid}}</span>
  <div class="card border-success mb-3" style="max-width: 18rem;" v-for="(list, i) in state.lists" :key="(list, i)">
  <div class="card-header" ref="nickname1">{{list.nickname}} 
  </div>
  <div class="card-body text-success">
    <h5 class="card-title" v-if="list.emailPublic==true"></h5>
    <!-- <h5 class="card-title" v-else>이메일: 비공개</h5> -->
    <p class="card-text content" v-if="list.studytopicPublic==true"></p>
    <!-- <p class="card-text " v-else>나의 관심: 비공개</p> -->
  </div>
  </div>
</div>
</template>
<script>
import { onMounted } from 'vue'
import {useRouter} from 'vue-router';
import { reactive,ref,toRef } from '@vue/reactivity'
import axios from 'axios';
export default {
  props: [ 'circleid' ],
  setup(props) {
    onMounted(() => { getData(); });
    const router = useRouter();
    const circleid = toRef(props,'circleid')
    let nickname1 = ref(null)
    const state = reactive({
      lists   : '',
      circle  :'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem('TOKEN'),
    });
    const modal = reactive({
      name  :'',
      uid   :''
    })
    const sendInfo =(name,uid)=>{
      modal.name = name
      modal.uid = uid
    }
    const getData = async () => {
      const url= "http://43.201.60.125/api/circleusers"
      const headers = {
        //TODO send the right amound of tokent and receive the same...
        "Content-Type" : "application/json",
        "Authorization": state.token,
        "token": state.token }
      const body = { circle: state.circle }
      console.log("body.circle :"+body.circle)
      await axios.post(url, body, { headers }).then(function (res) {
        if(res.data != null){
          state.lists = res.data
        } else {
          alert('bad.')
        }
      })
    }
      return{state,getData, modal, sendInfo,nickname1}
  }
}
</script>
<style scoped>
.card {
  border: solid black 1px;
  margin-left: 10px;
  margin-right: 10px;
  padding: 5px;
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
}
.card:hover {
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
  transform: scale(1.01);
}
.content{
  width: 400px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  word-break: break-all;
}
</style>