<template>
  <h2 hidden>{{state.circleid = $route.params.id }}</h2>
  <div class="row">
    <div class="card col">
      <h5 class="card-header">그룹 이름: {{state.name}} &nbsp;&nbsp;&nbsp;{{state.memberCount}}/{{state.maxNum}}<img class="image-people" alt="Vue logo"
          src="../assets/icones/people.jpg"></h5>
      <div class="card-body">
        <h5 class="card-title"></h5>
        <p v-if="state.content==''" class="card-text">그룹 설명 없음</p>
        <p v-else class="card-text">{{state.content}}</p>
        <button class="btn btn-light">
          <RouterLink to="/network" style="text-decoration:none;">리스트로 가기</RouterLink>
        </button>
        <span hidden v-if="state.isAmember==true"></span>
        <span v-else>
        <button class="btn btn-light" v-if="state.password != ''" data-bs-toggle="modal" data-bs-target="#exampleModal"
          @click="sendId(state.circleid, state.name, state.maxNum, state.memberCount)">가입</button>
        <button class="btn btn-light" v-else
          @click="sendId(state.circleid, state.name, state.maxNum, state.memberCount),join()">가입</button>
        </span>
      </div>

    </div>
    <!-- <h2>회원목록</h2> -->
    <div class="image-container col">
      <el-tooltip v-for="(list, i) in state.img" :key="(list, i)" effect="dark" v-bind:content="state.lists[i].nickname"
        placement="top">
        <img v-if="state.lists[i].path == null" src="../assets/images/pictures/profile_icon.png" @click="showUserList(i)">
        <img v-else="!(state.lists[i].path == null)" v-bind:src="state.img[i]" alt="프로필사진" @click="showUserList(i)">
      </el-tooltip>
    </div>
  </div>

  <div class="row">
    <div class="col">
      <NewsFeed
      :circleid="state.circleid"
       />
    </div>

    <div class="col" style="overflow-y: scroll;height: 600px;">
      <QuestionByCircle
      :circleid="state.circleid"
      :ismember="state.isAmember"
        />
    </div>
  </div>
  <!-- Modal -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel" ref="mTitle">비밀번호 확인<div>{{state.name}} 그룹에 가입</div>
          </h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="input-group input-group-sm mb-3">
            <span class="input-group-text" id="inputCircle-sizing-sm">비밀번호</span>
            <input type="pass" v-model="state.pass" class="form-control" aria-label="Sizing example input"
              aria-describedby="inputCircle-sizing-sm" autofocus>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">취소</button>
          <button type="button" class="btn btn-primary" data-bs-dismiss="modal" @click="passCheck(state.circleid)">확인</button>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { onMounted } from 'vue'
import { useRouter } from 'vue-router';
import NewsFeed from './TheNewsFeed.vue'
import CircleUserListForPublic from './CircleUserListForPublic.vue'
import QuestionByCircle from './QuestionByCircle.vue'
export default {
  components: { NewsFeed, QuestionByCircle, CircleUserListForPublic },
  setup() {
    onMounted(() => { getCircle(),isMember() })
    const router = useRouter();
    const state = reactive({
      name: '',
      content: '',
      maxNum: '',
      pass: '',
      password: '',
      circleid: 'circleid',
      lists: '',
      img: new Array(),
      isAmember:'',
      email: sessionStorage.getItem('email'),
      token: sessionStorage.getItem("TOKEN"),
    })
    const modal = reactive({
      id: '',
      name: '',
      maxNum: '',
      memberCount: ''
    })
    const isMember = async () => {
      console.log("readCircle launch")
      const url = "http://43.201.60.125/api/isMember"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid, email: state.email }
      console.log(" from isMember circleid: email: " + body.circleid + body.email)
      await axios.post(url, body, { headers }).then(function (res) {
        console.log('isMember res :'+res.data)
        state.isAmember = res.data
      }).catch((err)=>{console.log('err:'+err)})
    }
    const sendId = (id, name, maxNum, memberCount) => {
      modal.id = id
      modal.name = name
      modal.maxNum = maxNum
      modal.memberCount = memberCount
    }
    const getCircle = async () => {
      console.log("readCircle launch")
      const url = "http://43.201.60.125/api/circleread"
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token
      }
      const body = { circleid: state.circleid, email: state.email }
      console.log("circleid: email: " + body.circleid + body.email)
      await axios.post(url, body, { headers }).then(function (res) {
        console.log(res.data)
        if (res.status === 200) {
          state.name = res.data.dto.name;
          state.content = res.data.dto.content;
          state.maxNum = res.data.dto.maxNum;
          state.password = res.data.dto.pass;
          state.memberCount = res.data.dto.memberCount
          state.nickname = res.data.dto.nickname;
          state.uid = res.data.dto.uid;
          state.lists = res.data.img;
          showImage(state.lists)
        } else {
          alert('fail')
        }
      })
    }
    const join = async () => {
      console.log("join launched")
      if (modal.memberCount >= modal.maxNum) {
        alert("회원이 가득 찼습니다")
      } else {
        const url = 'http://43.201.60.125/api/joinCircle'
        const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
        }
        const body = { circleid: modal.id, email: state.email }
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.status === 200) {
            alert( res.data + "가입 되었습니다 "); router.push({ name: 'mycircleread', params: { id: modal.id } });
          }
        }).catch(function (error) {
          if (error) {
            console.log('Password is not correct from Join');
          }
        })
      }
    }
    const passCheck = async (circleid) => {
      if (modal.memberCount >= modal.maxNum) {
        alert("회원이 가득 찼습니다.")
      } else {
        const url = 'http://43.201.60.125/api/passCheck'
        const headers = {
          'Content-Type': 'application/json',
          Authorization: state.token,
          token: state.token,
        }
        const body = { circleid: circleid, pass: state.pass }
        await axios.post(url, body, { headers }).then(function (res) {
          if (res.data.dto.pass == state.pass) {
            state.pass = res.data.dto.pass
            state.circleid = res.data.dto.circleid
            join(res.data.dto.circleid);
          } else { alert("비밀번호가 맞지 않습니다"); }
        }).catch(function (error) {
          if (error) {
            alert('비밀번호가 맞지 않습니다!')
          }
        })
      }
    }
    const showImage = async (file) => {
      const url = `http://43.201.60.125/display`
      let str = "";
      console.info("Thumbnail" + file[0].thumbnailURL)
      console.info("Thumbnail" + file[1].thumbnailURL)
      for (let i = 0; i < file.length; i++) {
        str = `${url}?fileName=${file[i].thumbnailURL}`;
        state.img.push(str);
      }
      console.log(state.img)
    }
    const showUserList = async (i) => {
      console.log("클릭한 프로필 사람의 번호::" + state.lists[i].uid);
      console.log("로그인한 사람의 번호::" + state.uid);
      console.log(JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid));
      if (JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid)) {
        router.push({ name: "privateProfile" });
      } else if (!(JSON.stringify(state.uid) == JSON.stringify(state.lists[i].uid))) {
        router.push({ name: 'publicProfile', params: { id: state.lists[i].uid } });
      }
    }
    return { getCircle, state, passCheck, join,isMember, sendId, showImage, showUserList }
  }
}
</script>
<style scoped>

.col{
  margin-top: 30px;
}
.btn {
  margin: 0.7em;
}

.image-people {
  width: 1.4em;
}

.image-item {
  display: inline;
}

.image-container img {
  /* display: inl ine; */
  cursor: pointer;
}

.image-container {
  /* display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px 100px; */
  background-color: white;
  column-gap: 0px;
}
</style>